/**
 *  Page       
 */

class Page {
	constructor() {} // private _body: HTMLElement

	/**
	 *  setRightOrLeftProperty()       
	 * @returns string
	 */
	static setRightOrLeftProperty(additionalNum?: number): string {
		if (window.innerWidth > 1100) {
			return `${(window.innerWidth - 1110) / 2 + (additionalNum || 0)}px`;
		} else {
			return `15px`;
		}
	}

	/**
	 *  workingWithHeaderInfo()      ,
	 *           flowers
	 */
	static workingWithHeaderInfo(): void {
		if (window.innerWidth > 992) {
			(document.querySelector('.header__info') as HTMLElement)!.style.right =
				this.setRightOrLeftProperty(15);
		}
	}

	/**
	 *  windowResize()   window  resize 
	 *        
	 */
	static windowResize(): void {
		window.addEventListener('resize', () => this.workingWithHeaderInfo());
	}

	/**
	 *  bodyScroll()   window  scroll 
	 *       
	 * @param htmlElements '   -  flowersCart, flowersBody, body, headerContent, header, tel, infoTel,
	 */
	static bodyScroll(htmlElements: any): void {
		const {
				flowersCart,
				flowersBody,
				body,
				headerContent,
				header,
				tel,
				infoTel,
			} = htmlElements,
			flowersCartPosition: DOMRect = flowersCart.getBoundingClientRect();

		body.addEventListener('scroll', (): void => {
			if (window.innerWidth > 992) {
				if (body.scrollTop >= flowersCartPosition.y - header.clientHeight) {
					headerContent.append(tel);
					headerContent.append(flowersCart);
					flowersCart.style.alignSelf = 'center';
					tel.classList.add('moved');
					headerContent.style.alignItems = 'center';
				} else {
					infoTel.prepend(tel);
					flowersBody.append(flowersCart);
					flowersCart.style.alignSelf = 'flex-end';
					tel.classList.remove('moved');
					headerContent.style.alignItems = 'stretch';
				}
			} else {
				infoTel.prepend(tel);
				flowersBody.append(flowersCart);
				tel.classList.remove('moved');
				headerContent.style.alignItems = 'stretch';
			}

			if (body.scrollTop > 100) {
				header.style.backgroundColor = '#000';
			} else {
				header.style.backgroundColor = 'transparent';
			}
		});
	}
}

export default Page;
